<template>
  <section class="card">
    <div class="card-header">
      <div class="utils__title">
        <strong>Add an Admin</strong>
      </div>
    </div>
    <div class="card-body">
      <div :class="$style.addPost">
        <a-form class="mt-3" :form="form" @submit="handleSubmit">
          <div class="form-group">
            <a-form-item label="Admin Username">
              <a-input v-decorator="['username',{ rules: [{ required: true, message: 'Please provide admin username' }]}]" placeholder="Admin Username"/>
            </a-form-item>
            <a-form-item label="Admin Email">
              <a-input v-decorator="['email', { rules: [{type: 'email', message: 'The input is not valid E-mail!'},{ required: true, message: 'Please provide a valid email address' }]}]" placeholder="Admin Email"/>
            </a-form-item>
            <a-form-item label="Password">
              <a-input v-decorator="['password',{ rules: [{validator: validateToNextPassword},{ required: true, message: 'Please provide a password' }]}]" placeholder="Password" type="password"/>
            </a-form-item>
            <a-form-item label="Confirm Password">
              <a-input @blur="handleConfirmBlur" v-decorator="['confirm_password',{ rules: [{validator: compareToFirstPassword,},{ required: true, message: 'Please do confirm password' }]}]" placeholder="Confirm Password" type="password"/>
            </a-form-item>
            <a-form-item label="User Role">
              <a-select v-decorator="['role',
                { rules: [{ required: true, message: 'Please give this user a role!' }] },
              ]" placeholder="Apply user role">
                <a-select-option value="1" selected>Super Admin</a-select-option>
                <a-select-option value="2">Manager</a-select-option>
                <a-select-option value="3">Editor</a-select-option>
              </a-select>
            </a-form-item>
            <br>
            <div class="form-group">
              <a-form-item>
                <div :class="$style.submit">
                  <span class="mr-3">
                    <a-button type="primary" htmlType="submit">Add User</a-button>
                  </span>
                  <a-button @click="handleReset" type="danger">Discard</a-button>
                </div>
              </a-form-item>
            </div>
          </div>
        </a-form>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'create',
  data() {
    return {
      form: this.$form.createForm(this),
    }
  },
  methods: {
    handleReset() {
      this.form.resetFields()
    },
    handleSubmit(e) {
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (!err) {
          this.$nprogress.start()
          const requestPayload = {
            username: values.username,
            email: values.email,
            password: values.password,
            password_confirmation: values.confirm_password,
            user_type: values.role,
          }
          this.axios.post('user/register', requestPayload)
            .then((response) => {
              this.$nprogress.done()
              // console.log(response)
              if (response.status === 200) {
                this.handleReset()
                this.$notification['success']({
                  message: 'Customer Creation',
                  description: 'Customer has been created',
                })
              }
              return response
            })
            .catch((error) => {
              this.$nprogress.done()
              // console.log(error)
              return error
            })
        }
      })
    },
    handleConfirmBlur(e) {
      const value = e.target.value
      this.confirmDirty = this.confirmDirty || !!value
    },
    compareToFirstPassword(rule, value, callback) {
      const form = this.form
      if (value && value !== form.getFieldValue('password')) {
        // eslint-disable-next-line standard/no-callback-literal
        callback('Two passwords that you enter is inconsistent!')
      } else {
        callback()
      }
    },
    validateToNextPassword(rule, value, callback) {
      const form = this.form
      if (value && this.confirmDirty) {
        form.validateFields(['confirm'], { force: true })
      }
      callback()
    },
  },
}
</script>

<style lang="scss" module>
  @import "./style.module.scss";
</style>
